        //这个阈值要和视窗大小绑定在一起
        this.currentScroll = window.scrollY;
        if (this.currentScroll >= this.$refs.introcard.offsetTop -250 && this.currentScroll<= (this.$refs.introcard.offsetTop + this.$refs.introcard.offsetHeight -250)){
          this.introcardflag = true
          this.introletterflag = true
        }else{
          this.introcardflag = false
          this.introletterflag = false
        }
        /*if (this.currentScroll >= this.$refs.introletter.offsetTop -250 && this.currentScroll<= (this.$refs.introletter.offsetTop + this.$refs.introletter.offsetHeight -250)){
          this.introletterflag = true
        }else{
          this.introletterflag = false
        }*/
        if (this.currentScroll >= this.$refs.purposecard.offsetTop -250 && this.currentScroll<= (this.$refs.purposecard.offsetTop + this.$refs.purposecard.offsetHeight -250)){
          this.purposecardflag = true
          this.purposeletterflag = true
        }else{
          this.purposecardflag = false
          this.purposeletterflag = false
        }
        /*if (this.currentScroll >= this.$refs.purposeletter.offsetTop -250 && this.currentScroll<= (this.$refs.purposeletter.offsetTop + this.$refs.purposeletter.offsetHeight -250)){
          this.purposeletterflag = true
        }else{
          this.purposeletterflag = false
        }*/
        if (this.currentScroll >= this.$refs.recruitcard.offsetTop -250 && this.currentScroll<= (this.$refs.recruitcard.offsetTop + this.$refs.recruitcard.offsetHeight -250)){
          this.recruitcardflag = true
          this.recruitletterflag = true
        }else{
          this.recruitcardflag = false
          this.recruitletterflag = false
        }
        /*if (this.currentScroll >= this.$refs.recruitletter.offsetTop -250 && this.currentScroll<= (this.$refs.recruitletter.offsetTop + this.$refs.recruitletter.offsetHeight -250)){
          this.recruitletterflag = true
        }else{
          this.recruitletterflag = false
        }*/
        if (this.currentScroll >= this.$refs.awardcard.offsetTop -250 && this.currentScroll<= (this.$refs.awardcard.offsetTop + this.$refs.awardcard.offsetHeight -250)){
          this.awardcardflag = true
          this.awardletterflag = true
        }else{
          this.awardcardflag = false
          this.awardletterflag = false
        }
        if (this.currentScroll >= this.$refs.productcard.offsetTop -250 && this.currentScroll<= (this.$refs.productcard.offsetTop + this.$refs.productcard.offsetHeight -250)){
          this.productcardflag = true
          this.productletterflag = true
        }else{
          this.productcardflag = false
          this.productletterflag = false
        }
      },
      clickHandler(data) {
        this.goAnchor(data.value)
      },
    },
    mounted(){
      window.addEventListener('scroll',this.handleScroll)
    },
  }
