          <t-divider style="width: 200px;position: absolute;top: 5px;">选择项目</t-divider> 
          <t-dropdown :options="options" @click="dropdownbuttonhandler" style="position: absolute;top: 60px;">
              <t-button varient="text">
                  <span class="tdesign-demo-dropdown__text">
                  选择正在招新的项目
                  <chevron-down-icon size="16" />
                  </span>
              </t-button>
            </t-dropdown>
        </div>
        <div style="width: 1.5px;height: 580px;background-color: whitesmoke;position: absolute;top: 10px;left: 320px;"></div>
        <div style="width: 660px;height: 580px;position: absolute;top: 10px;right: 10px;display: flex;justify-content: center;overflow-y: auto;">
          <div style="display: flex;flex-direction: column;">
            <div class="card" v-for="item in data[selectProject]">
            <P style="color: blue;font-weight: bold;width: 200px;margin-left: 20px;">岗位名称</P>
            <p style="width: 550px;margin-left: 20px">
