            {title:"建模&地编(FPS项目)",description:"（1）学习使用3dmax/cinema 4D进行角色&环境建模（2）学习关卡设计、游戏场景设计相关知识，根据剧本设计交互性较好的游戏场景（3）参与工作室未来参赛/商业化项目中建模&地编相关内容的开发",require:"（1）需要有一定美术基础，推荐建艺学院未来想进入这个领域的同学报名（2）具有3dmax/cinema 4D/maya/Blender经验的同学优先（3）报名表中可阐述FPS游戏游玩经历&其中让你感到深刻的场景设计"},
            {title:"动画（FPS项目）",description:"（1）学习使用Maya等软件手动K帧制作角色动画（2）参与工作室未来参赛/商业化项目中动画相关内容的开发",require:"报名无专业限制"},
            {title:"策划&剧本、演出设计（FPS项目）",description:"（1）密切关注行业动态，发掘新游戏形式和题材（2）学习游戏剧本、演出设计相关知识（3）参与工作室未来参赛/商业化项目中剧本，演出设计相关内容的开发",require:"（1）报名无专业限制（2）具有丰富游戏经验的同学优先（3）报名表中可阐述你心目中比较创新的FPS游戏形式（玩法）以及你的新想法、创新点等，你心目中影响最深刻的FPS游戏剧本、演出设计，以及对该剧本、演出的理解（注：演出指的是游戏中和npc对话时/执行任务时动画&过场动画等内容）"},
        ],[
            {title:"引擎主程序（动作游戏项目）",description:"（1）学习Unity/Unreal引擎操作与性能优化（2）学习使用C#/C++开发游戏脚本（3）学习动作游戏常见系统设计（4）学习网络编程等多人在线游戏技术（5）参与工作室未来参赛/商业化动作游戏开发",require:"报名表中可阐述动作游戏游玩经历&自己对动作游戏这一类目的新想法、创新点或理解"},
            {title:"技术美术(Unity)【动作游戏项目】",description:"（1）学习使用C#语言进行编程（2）学习Unity Shader,Shader Graph等可编程着色器开发（3）学习Unity中水体渲染，植物渲染，动态天空盒，大气渲染，卡通渲染等具体技术的实现（4）学习使用Unity粒子系统，Visual Effect Graph等系统进行特效的开发（5）参与工作室未来参赛/商业化项目中特效&水体渲染&动态天空盒等内容的开发",require:"需要有一定美术基础，推荐建艺学院未来想进入这个领域的同学报名。"},
            {title:"建模&地编(动作游戏项目)",description:"（1）学习使用3dmax/cinema 4D进行角色&环境建模（2）学习关卡设计、游戏场景设计相关知识，根据剧本设计交互性较好的游戏场景（3）参与工作室未来参赛/商业化项目中建模&地编相关内容的开发",require:"（1）需要有一定美术基础，推荐建艺学院未来想进入这个领域的同学报名（2）具有3dmax/cinema 4D/maya/Blender经验的同学优先（3）报名表中可阐述动作游戏游玩经历&其中让你感到深刻的场景设计"},
            {title:"动画（动作游戏项目）",description:"（1）学习使用Maya等软件手动K帧制作角色动画（2）参与工作室未来参赛/商业化项目中动画相关内容的开发",require:"报名无专业限制"},
            {title:"策划&剧本、演出设计（动作游戏项目）",description:"（1）密切关注行业动态，发掘新游戏形式和题材（2）学习游戏剧本、演出设计相关知识（3）参与工作室未来参赛/商业化项目中剧本，演出设计相关内容的开发",require:"（1）报名无专业限制（2）具有丰富游戏经验的同学优先（3）报名表中可阐述你心目中比较创新的动作游戏形式（玩法）以及你的新想法、创新点等，你心目中影响最深刻的动作游戏剧本、演出设计，以及对该剧本、演出的理解（注：演出指的是游戏中和npc对话时/执行任务时动画&过场动画等内容）"},
        ],[
            {title:"惯性/光学动作捕捉系统项目",description:"（1）尝试使用惯性传感器、单片机等硬件资源，构建基于惯性/光学的动作捕捉系统（精度不需要很高）（2）尝试应用最新的单目动作识别AI算法，并应用于项目上（3）代表工作室使用该硬件项目参与大创、FPGA大赛等等硬件比赛",require:"（1）推荐科中硬件组成员/电子信息工程、自动化或电气工程等硬件相关专业同学，人工智能专业同学报名（2）报名表中请写明具体硬件技能（如STM32，ESP8266开发经验等（3）有参加过电子设计竞赛等实际开发经验者优先"},
            {title:"虚拟现实交互设备系统项目",description:"（1）尝试在现有VR，AR眼镜等技术基础上，创新交互形式（游戏方向），并使用简单零件开发原型产品（2）代表工作室使用该硬件项目参与大创、集创赛、FPGA大赛等硬件比赛",require:"（1）推荐科中硬件组成员/电子信息工程、自动化或电气工程等硬件相关专业同学，人工智能专业同学报名（2）报名表中请写明具体硬件技能（如STM32，ESP8266开发经验等（3）有参加过电子设计竞赛等实际开发经验者优先"},
            {title:"AIGC相关项目",description:"（1）尝试学习、部署并推进LLM(chatGLM等)在游戏领域应用【如npc控制等】（nlp方向）（2）尝试学习、尝试部署并推进文生3D/图生3D模型融入游戏开发工具链（cv方向）（3）代表工作室发布AIGC In Game相关成果、技术文档(如上传Unity Asset Store作为插件收费、Github开源等)",require:"推荐科中软件组人工智能方向同学，人工智能专业同学报名"},
        ],[
            {title:"新媒体运营",description:"（1）参与工作室B站/抖音等官方账号运营工作",require:"（1）报名无专业限制（2）有B站up主等实际运营经历者优先"},
            {title:"视频剪辑",description:"（1）学习视频剪辑相关技能（2）参与工作室宣传片/pv等视频素材剪辑工作",require:"（1）报名无专业限制（2）具有视频剪辑经验者优先"},
            {title:"网页前端",description:"（1）学习前端技术栈：Vue.js/React.js Javascript/TypeScript html css等（2）参与工作室官网和相关网页前端开发工作",require:"（1）推荐科中软件组web方向同学，计算机科学与技术专业同学报名（2）具有实际前端开发经验者优先"},
            {title:"网页后端",description:"（1）学习后端技术栈: Golang mysql/redis java等（2）参与工作室官网和相关网页后端开发工作",require:"（1）推荐科中软件组web方向同学，计算机科学与技术专业同学报名（2)具有实际后端开发经验者优先"}
        ],[]],
        selectProject:6
      }
    },
    methods:{
       goAnchor(id) {
        var anchor = document.getElementById(id);
        anchor.scrollIntoView();
       },
       changeHandler(active){
        this.goAnchor(active)
       },
       goToHomePage(){
        this.$router.push('/')
       },
       dropdownbuttonhandler(data){
        console.log(data)
        this.selectProject = data.value
       }
    }
  }
  </script>
  
  <style>
  .page{
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow-x: hidden;
    background-color: #FBFBFB;
  }
  .footer{
    margin-top: auto;
    width:100vw;
    height: 60px;
    background: linear-gradient(to right,whitesmoke,white);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .footer_item{
    color: grey;
    font-size: 13px;
  }
  .scrolldiv{
    width: 100vw;
    height: 90vh;
    overflow-y: scroll;
  }
  .card{
    width: 640px;
    height: 600px;
    border-radius: 15px;
    border: solid 1.5px whitesmoke;
    background-color: white;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  .card:hover{
    border: solid 1.5px #61CBF4;
  }
  </style>


