import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/pages/HomePage'
import recruit from '@/pages/recruit'
import resource from '@/pages/resource'

Vue.use(Router)

export default new Router({
  //mode:"history",
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage
    },{
      path: '/recruit',
      name: 'recruitment',
      component: recruit
    },{
      path: '/resource',
      name: 'resource',
      component: resource
    }
  ],
})



