<template>
    <div class="page">
      <t-head-menu theme="light" height="120px" style="box-shadow: 0 -1px 3px 0.1px;">
        <img slot="logo" height="60" src="../assets/EGS-logo.png" alt="logo" @click="goToHomePage"/>
        <p style="color: #1153FC;font-size: 25px;font-weight: bold;">资源网</p>
      </t-head-menu>
      <div style="writing-mode: vertical-lr;position:absolute;right: -70px;top: 250px;height: 350px;">
          <p style="font-size: 60px;font-weight: bold;">RESOURCE</p>
          <div style="width: 10px;height: 350px;position: relative;right: 140px;top: -10px;background-color:#FF7B02 ;"></div>
          <p style="position: relative;right: 200px;top: 165px;font-size: 20px;font-weight: bold;color: grey;">Elab Game Studio</p>
      </div>
      <div style="left: -300px;top: 35px;position: relative;width: 300px;height: 50px;display: flex;justify-content: center;align-items: center;border: 5px solid #FFCB52;border-radius: 20px;">
            <p style="color: #FF7B02;font-size: 30px;font-weight: bold;">教程视频等资源下载</p>
      </div>
      <t-dropdown :options="options" @click="dropdownHandler" style="position: relative;top: 50px;right: 380px;">
            <t-button varient="text">
                <span class="tdesign-demo-dropdown__text">
                选择教程章节
                <chevron-down-icon size="16" />
                </span>
            </t-button>
      </t-dropdown>
      <div style="width: 100vw;height: 630px;">
        <video width="750px" height="375px" controls style="position: relative;top: 80px;left: 375px;" ref="video">
            <source src="static/video_test.mp4" type="video/mp4" ref="videosource">
        </video>
      </div>
      <div style="width: 850px;height: 600px;border-radius: 10px;margin-bottom: 250px;position: relative;top: 30px;background: linear-gradient(to bottom right,#5581F1,#1153FC);">
          <div style="width: 10px;height: 10px;position: relative;top:-100px;left: 155px;" id="recruit"></div>
          <t-button theme="primary" style="position: relative;left: 180px;top: 25vh;z-index: 1000;">了解详情</t-button>
          <div style="position: relative;left: 5.5vw;top: -13vh;width: 150px;height: 50px;display: flex;justify-content: center;align-items: center;border: 5px solid #5581F1;border-radius: 20px;">
            <p style="color: #1153FC;font-size: 30px;font-weight: bold;">章节详情</p>
          </div>
          <p style="color: white;font-size: 15px;font-weight: bold;width: 400px;height: 500px;position: relative;left: 60px;top: -50px;" ref="info">
            测试视频
          </p>
          <div style="background-color: #CCFFFF;width: 250px;height: 250px;position: relative;left: 725px;bottom: 250px;border-radius: 15px;"></div>
    </div>
        <t-back-top
        :visible-height="0"
        style="position:fixed;
        bottom: 10vh;
        right: 1vw;"
      ></t-back-top>
      <div class="footer">
          <p class="footer_item">滇ICP备2024019357号 © 2024 Elab Game Studio 版权所有</p>
      </div>
    </div>
  </template>
  
  <script>
  import { ChevronDownIcon } from 'tdesign-icons-vue'; 

  export default {
    components:{
        ChevronDownIcon,
    },
    name: 'Resource',
    data(){
      return {
        options: [
            {
                content: '教程一',
                value: 0 //这里填视频的url地址
            },
            {
                content: '教程二',
                value: 1
            },
            {
                content: '教程三',
                value: 2
            },
            {
                content: '教程四',
                value: 3
            }
        ],
        videoInfoList: [
            {
                url: 'static/video_test.mp4',
                info: '测试视频'
            },
            {
                url: 'static/video_test.mp4',
                info: '第二个测试视频'
            },
            {
                url: 'static/video_test.mp4',
                info: '第三个测试视频'
            },
            {
                url: 'static/video_test.mp4',
                info: '第四个测试视频'
            }
        ],
      }
    },
    methods:{
       goToHomePage(){
        this.$router.push('/')
       },
       dropdownHandler(data){
        this.$refs.video.src=this.videoInfoList[data.value].url
        this.$refs.videosource.src=this.videoInfoList[data.value].url
        this.$refs.info.innerHTML = this.videoInfoList[data.value].info
       },
    }
  }
  </script>
  
  <style>
  .page{
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow-x: hidden;
  }
  .footer{
    margin-top: auto;
    width:100vw;
    height: 60px;
    background: linear-gradient(to right,whitesmoke,white);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .footer_item{
    color: grey;
    font-size: 13px;
  }
  .scrolldiv{
    width: 100vw;
    height: 90vh;
    overflow-y: scroll;
  }
  .card{
    width: 85vw;
    height: 60vh;
    border-radius: 10px;
    box-shadow: 1px 1px 3px grey;
    margin-bottom: 100px;
  }
  </style>


